<template>
  <div
    class="container"
    :class="{ 'answer-ok': answer === true, 'answer-error': answer === false }"
  >
    <div>
      <img :src="image" alt="ball" />
    </div>
    <div v-show="answer === true">
      <span> Ball</span>
    </div>
    <div>
      <span>
        Ваша відповідь:
        <br />
        <input type="text" v-model="answer" placeholder="input your answer" />
      </span>
    </div>
    <button @click="translate()">Перевірити</button>
    <div>
      <span>
        {{ result }}
      </span>
    </div>
  </div>
</template>

<script>
// Задача 3. Елемент тренажера англійської. Виводимо зображення елемента і слово.
// Корисувач вводить відповідь. Якщо вірно – відтворюємо фразу «Добре. Молодець!»
// (і додаємо зелену рамку до елемента)
//  якщо ні - то відтворюємо фразу «Невірно, спробуйте ще раз» (і додаємо червону рамку).
export default {
  name: "EnglishSimulator",
  props: {
    image: {
      type: URL,
      default:
        "data:image/jpeg;base64,",
    },
  },
  data() {
    return {
      answer: null,
      result: null,
    };
  },
  methods: {
    translate() {
      if (this.answer === "ball") {
        this.answer = null;
        this.result = "Добре. Молодець!";
        this.answer = true;
      } else {
        this.result = "Невірно, спробуйте ще раз";
        this.answer = false;
      }
    },
  },
};
</script>

<style lang="css" scoped>
.container {
  width: max-content;
  height: max-content;
  border: black solid;
}
.answer-ok {
  border: green solid;
}
.answer-error {
  border: red solid;
}
</style>
